import { ContentState, EditorState } from 'draft-js';
import editorjsHTML from 'editorjs-html';
import htmlToDraft from 'html-to-draftjs';
import React from 'react';
import EditorJs from 'react-editor-js';
import { alertBox, switchLoader } from '../../../commonRedux/';
import Button from '../../../components/Button';
import Spinner from '../../../components/Spinner';
import { createGigRequest, getCategories, getGigRequest, getGigRequestDraft } from '../../../http/gig-calls';
import { EDITOR_JS_TOOLS } from '../../MyBlogs/tools';

require ("./styles.scss");
let coins = require('./coins.json');

class GigRequestTab extends React.Component {
  constructor(props) {
    super(props);
    const edjsParser = editorjsHTML();
    const html = '{"time":1612692654392,"blocks":[{"type":"paragraph","data":{"text":"sdff"}},{"type":"paragraph","data":{"text":"<b>sdfsdf</b>"}}],"version":"2.19.1"}';
    const contentBlock = htmlToDraft(html);
    this.editor = React.createRef();
    this.reactTags = React.createRef()

    var editor = null;
    if (contentBlock) {
      const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks);
      const editorState = EditorState.createWithContent(contentState);
      editor = editorState;
    }
      this.state = {
        id:0,
        ide:false,
        loading:true,
        editorState: editor,
        editorInstance:null,
        edjsParser: edjsParser,
        tags: [],
        id:0,
        level: 1,
        dragclass: '',
        parent: "",
        files: [],
        files2: [],
        features: [{feature: '', standard:0, premium: 1}],
        extras: [{feature: '', value: '', amount: ''}],
        level1: [],
        level2: [],
        level3: [],
        level4: [],
        level5: [],
        coins : coins,
        title: '',
        currency: "",
        category: "",
        category_id: "",
        sub_category: "",
        sub_category_id: "",
        format: "",
        maxGigs: 0,
        delivery: "",
        budget: null,
        gallery:[]
      };
  }
  onEditorStateChange = (editorState) => {
    this.setState({
      editorState,
    });
  };

  onDelete (i) {
    const tags = this.state.tags.slice(0)
    tags.splice(i, 1)
    this.setState({ tags })
  }
 
  onAddition (tag) {
    const tags = [].concat(this.state.tags, tag)
    this.setState({ tags })
  }

  tagChange = (val) => {
    console.log(val)
  }

  handleFeature = (i, e) => {
    var name =  e.target.name;
    var temp = this.state.features;
    if(name == 'feature'){
      var val =  e.target.value;
      temp[i][name] = val;
    }else{
      var val =  e.target.checked;
      temp[i][name] = val ? 1 : 0;
    }
    console.log(temp);
    this.setState({
        features : temp
    })
  };

  addFeature = () => {
    var temp = this.state.features;
    temp.push({feature: '', standard: 0, premium : 1});
    this.setState({
      features: temp
    })
  }

  removeFeature = (i) => {
    var temp = this.state.features;
    delete temp[i];
    this.setState({
      features: temp
    })
  }

  handleExtra = (i, e) => {
   
    var name =  e.target.name;
    var temp = this.state.extras;
    var val =  e.target.value;
    temp[i][name] = val;
    console.log(temp);
    this.setState({
        extras : temp
    })
  };

  addExtra = () => {
    var temp = this.state.extras;
    temp.push({feature: '', value: '', amount : ''});
    this.setState({
      extras: temp
    })
  }

  removeExtra = (i) => {
    var temp = this.state.extras;
    delete temp[i];
    this.setState({
      extras: temp
    })
  }


  componentDidMount() {
   this.getGigCategories(1);
  //  if(this.props.match.params.id != undefined && this.props.match.params.id != null){
  //    this.getData(this.props.match.params.id);
  //  }
   if(this.props.id != undefined && this.props.id != null){
    this.getData();
  }else{
    this.setState({
      loading:false
    })
  }
  this.getGigCategories(1); 
  }

  getGigCategories = (level) => {
    getCategories({level: level, parent: this.state.parent}).then(resp => {
      this.setState({
        ["level"+level] : resp
      })
    }, err => {
      console.log(err);
    })
  }

  getData = () => {
    getGigRequest({id: this.props.id}).then(res=>{
      try {
        var resp = res.post;
        this.setState({
            id: resp._id,
            title: resp.subject,
            data: JSON.parse(resp.editorContent),
            currency: resp.preferedCurrency,
            desc: resp.text,
            extras: resp.extras,
            budget: resp.budget,
            delivery: resp.duration,
            maxGigs: resp.maxGigs,
            files2: resp.contents,
            category: resp.category,
            category_id: resp.categoryId,
            sub_category: resp.subCategory,
            sub_category_id: resp.subCategoryId,
          },() => {
            setTimeout(() => {
              this.setState({
              loading:false,
              })
            }, 1000);
            
          })
      } catch (error) {
        this.setState({
          ide:true,
          loading:false,
          })
      }
  
     }, err=>{
         this.setState({loading:false})
     });
  }

  getDraft = () => {
    getGigRequestDraft().then(res=>{
      var resp = res;
      this.setState({
          id: resp._id,
          title: resp.subject,
          data: JSON.parse(resp.editorContent),
          currency: resp.preferedCurrency,
          desc: resp.text,
          extras: resp.extras,
          budget: resp.budget,
          delivery: resp.duration,
          maxGigs: resp.maxGigs,
          files2: resp.contents,
          category: resp.category,
          category_id: resp.categoryId,
          sub_category: resp.subCategory,
          sub_category_id: resp.subCategoryId,
      },() => {
        setTimeout(() => {
          this.setState({
          loading:false,
          })
        }, 1000);
        
      })
     }, err=>{
      this.setState({loading:false})
     });
  }

  getGigCategories = (level) => {
    getCategories({level: level, parent: this.state.parent}).then(resp => {
      this.setState({
        ["level"+level] : resp
      })
    }, err => {
      console.log(err);
    })
  }
  

  handleInput = (e) => {
    var val =  e.target.value;
    var name =  e.target.name;
    if(name == 'title'){
      console.log(val.length);
      if(val.length > 2500){
        console.log("over")
        val = val.substring(0, 2500);
        console.log(this.state.title);
        console.log(val);
      }
      this.setState({
          [name] : val
      })
    }else{
      this.setState({
        [name] : val
      })
    }
  };


  handleCategory = (val, i) => {
      
    var v = val.split("^");
    if(i == 2){
      this.setState({
        category: v[1],
        category_id: v[0],
        parent: v[0],
        level:i
      }, () => {
        if(i < 6){
        this.getGigCategories(i);
        }
      });
    }else{
      this.setState({
        sub_category: v[1],
        sub_category_id: v[0],
        parent: v[0],
        level:i
      }, () => {
        if(i < 6){
        this.getGigCategories(i);
        }
      });
    }

  
};


  handleFile = (name, type, value) => {
    console.log(value);
    this.setState({
        [name] : value
    },()=>{
      var reader  = new FileReader();
    // it's onload event and you forgot (parameters)
      reader.onload = function(e)  {
          var image = document.getElementById(name+"_preview");
          image.src = e.target.result;
      }
      // you have to declare the file loading
      reader.readAsDataURL(value);
    })
  };
  

  submit = async (e, t) => {
    e.preventDefault();
    var edata = await this.editor.save();
    var err = [];
    var status = 'active';
    console.log(t);
    if(t == 1){
    if(this.state.title == ''){
      err.push("Title is required");
    }
    if(this.state.category == ''){
      err.push("Category is required");
    }
    if(this.state.files.length == 0 && this.state.files2.length == 0){
      err.push("At least one gallery image is required");
    }
  }else{
    if(this.state.title == ''){
      err.push("Title is required");
    }
    status = 'draft';
  }

    if(err.length > 0){
      alertBox(true, err.join(", "));
    }
    else {
      var data = this.state.edjsParser.parse(edata);
      data = data.join(" ");
      switchLoader("Posting request, please wait...");
      var formData = {
        status: status,
        subject: this.state.title,
        category: this.state.category,
        category_id: this.state.category_id,
        sub_category: this.state.sub_category,
        sub_category_id: this.state.sub_category_id,
        maxGigs: this.state.maxGigs == "" || this.state.maxGigs == null ? 0 : this.state.maxGigs,
        currency: this.state.currency,
        budget: this.state.budget,
        delivery: this.state.delivery,
        text: data,
        editorContent: JSON.stringify(edata),
        files: this.state.files,
        files2: this.state.files2
      };

      if(this.props.id != undefined && this.props.id != null && !this.state.ide){
        formData.id = this.props.id;
      }
      if(this.state.id != 0 && !this.state.ide){
        formData.id = this.state.id;
      }

      console.log(formData);
 
      createGigRequest(formData)
      .then(async resp => {
        
        if(t==1){
          window.location.href="/passionomy/dashboard/requests";
          //this.props.history.push("/passionomy/my-requests");
        }else{
          alertBox(true, "Draft has been saved!", "success");
          this.props.history.push("/passionomy/request/edit/"+resp.post._id);
        }
    
        switchLoader();
      }, error => {
        alertBox(true, error.message);
        switchLoader();
      });
    }
  }
  

  handleDragEnter = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: 'enter'
    })
  };
  handleDragLeave = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: ''
    })
  };
  handleDragOver = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: 'over'
    })
  };
  handleDrop = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: ''
    })
    let dt = e.dataTransfer
    let files = dt.files
    this.handleFiles(files);
  };

  addToGallery = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: ''
    })
    console.log(e.target.files);
    this.handleFiles(e.target.files);
  };
  
  handleClick = (e) => {
    e.preventDefault();
    e.stopPropagation();
    var el = document.querySelector(".droppedFiles");
    if(!el.contains(e.target)){
    document.getElementById('gallery').click();
    }
  };

  fileToDataURL = (file) => {
    var reader = new FileReader()
    return new Promise(function (resolve, reject) {
      reader.onload = function (event) {
        resolve(event.target.result)
      }
      reader.readAsDataURL(file)
    })
  }  
  
  readAsDataURL = (filesArray) => {
    // target => <input type="file" id="file">
    // console.log(target);
    // var filesArray = Array.prototype.slice.call(target.files)      
    return Promise.all(filesArray.map(this.fileToDataURL))
  }
  handleFiles = async (files) => {
  
    var temp = [...this.state.files, ...files];
    console.log(temp);
    var f = await this.readAsDataURL(temp);
    console.log(f);
    this.setState({
      files:  temp,
      imgs: f
    })
  }

  removeFile = (i, n=1) => {
    if(n==2){
      var temp = this.state.files2;
      temp.splice(i, 1);
      this.setState({
        files2:temp,
      })
    }else{
    var temp = this.state.files;
    var temp2 = this.state.imgs;
    temp.splice(i, 1);
    temp2.splice(i,1);
    this.setState({
      files:temp,
      imgs:temp2
    })
  }
  }
 
    render() {
    
        return (
          
                <div className="">
                  
                  <form onSubmit={(e) => this.submit(e, 1)} method="post">
                  <div className="form-group clearfix">
                      <div><strong>I'm Looking For...</strong></div>
                      <input type="text" required placeholder="The service you're looking to purchase" name="title" className="form-control w-100" onChange={this.handleInput} value={this.state.title} />
                                <span className="add-comment pull-right">
                                    {this.state.title.length} / 100 max
                                </span>
                      </div>
                      <div className="row">
                        <div className="col-md-6">
                          <div className="form-group">
                            <div className="left">
                              <strong>Category</strong>
                              </div>
                              <select type="text" placeholder="I will help you for..." name="category" className="form-control w-100" onChange={(e) => this.handleCategory(e.target.value, 2)} >
                                  <option value="">Select</option>
                                  {
                                    this.state.level1.map((c, k) => {
                                      return <option key={k} value={c._id+"^"+c.category} selected={this.state.category == c.category}>{c.category}</option>
                                    })
                                  }
                                </select>
                          </div>
                        </div>
                        <div className="col-md-6">
                          <div className="form-group">
                              <div className="left">
                                <strong>Sub Category</strong>
                                </div>
                                
                                  <select type="text" placeholder="I will help you for..." name="category" className="form-control w-100" onChange={(e) => this.handleCategory(e.target.value, 3)} >
                                  <option value="">Select</option>
                                  {
                                    this.state.level2.length > 0 && this.state.level2.map((c, k) => {
                                      return <option key={k} value={c._id+"^"+c.category} selected={this.state.sub_category == c.category}>{c.category}</option>
                                    })
                                  }
                                </select>
                            </div>
                        </div>
                      </div>
                      <div className="row">
                        <div className="col-md-6">
                          <div className="form-group">
                            <div className="left">
                            <strong>Maximum Gigs Required</strong>
                              </div>
                              <div className="right d-flex align-items-center justify-content-between">
                                <div><input type="checkbox" checked={this.state.maxGigs === 0 ? true: false} onClick={()=>{ 
                                  if(this.state.maxGigs == 0){
                                    this.setState({maxGigs: null})
                                  }else{
                                    this.setState({maxGigs: 0})
                                  }
                                  }} /> </div> 
                                  <div className="pl-3 pr-3">Infinity</div>
                                <input type="number" disabled={this.state.maxGigs === 0 ? true: false}  required name="maxGigs" placeholder="or Enter the limit in numbers" className="form-control w-100" value={this.state.maxGigs} onChange={this.handleInput} />
                              </div>
                          </div>
                        </div>
                        <div className="col-md-6">
                          <div className="form-group">
                              <div className="left">
                              <strong>Delivery Duration</strong>
                                </div>
                                
                                <div className="right">
                                <input type="number"  required placeholder="in Days" name="delivery" className="form-control w-100" value={this.state.delivery} onChange={this.handleInput} />
                              </div>
                            </div>
                        </div>
                      </div>

                      <div className="row">
                        <div className="col-md-6">
                          <div className="form-group">
                            <div className="left">
                            <strong>Preferred Currency</strong>
                              </div>
                              <div className="right">
                                <select name="currency" value={this.state.currency} required className="form-control w-100" onChange={this.handleInput} >
                                  <option value="">Select</option>
                                  {
                                    this.state.coins.map((e, i) => {
                                      return <option value={e.currencySymbol}>{e.currencyName+" ("+e.currencySymbol+")"}</option>
                                    })
                                  }
                                </select>
                              </div>
                          </div>
                        </div>
                        <div className="col-md-6">
                          <div className="form-group">
                              <div className="left">
                              <strong>Budget</strong>
                                </div>
                                
                                <div className="right">
                                <input type="number" value={this.state.budget}  required placeholder="Amount" name="budget" className="form-control w-100" onChange={this.handleInput} />
                              </div>
                            </div>
                        </div>
                      </div>
                 
                      <div className="form-group">
                   <div className="left">
                              <strong>Description</strong>
                              </div>
                              <div className="right">
                              {
                     this.state.loading ? 
                     <Spinner />
                     : <EditorJs instanceRef={instance => {this.editor = instance}} data={this.state.data} tools={EDITOR_JS_TOOLS} /> }

                                  {/* <textarea style={{display:'none'}}
                                    disabled
                                    value={draftToHtml(convertToRaw(this.state.editorState.getCurrentContent()))}
                                  /> */}
                                {/* <textarea type="text" required placeholder="Description about this gig" name="desc" className="form-control w-100" onChange={this.handleInput} >{this.state.desc}</textarea> */}
                              </div>
                   </div>

                   <div className="form-group">
                      <div className="left">
                              <strong>Reference Images</strong>
                              </div>
                              <div className="right">
                                <div className={"uploadArea "+this.state.dragclass} onDrop={e => this.handleDrop(e)}
                                  onDragOver={e => this.handleDragOver(e)}
                                  onDragEnter={e => this.handleDragEnter(e)}
                                  onDragLeave={e => this.handleDragLeave(e)}
                                  onClick={e => this.handleClick(e)}
                                  >
                                    <div className="droppedFiles">
                                    {
                                        this.state.files2.map((f, i)=>{
                                          return <div className="file">
                                            <img src={f.content_url !=undefined ? f.content_url : this.state.imgs[i]} />
                                            <i className="fa fa-times" onClick={()=>this.removeFile(i, 2)}></i>
                                          </div>
                                        })
                                      }
                                    {
                                      this.state.files.map((f, i)=>{
                                        return <div className="file">
                                          <img src={this.state.imgs[i]} />
                                          <i className="fa fa-times" onClick={()=>this.removeFile(i)}></i>
                                        </div>
                                      })
                                    }
                                  </div>
                                  Browse / Drag and Drop
                                  
                                  
                                </div>
                                <input type="file" id="gallery" onChange={this.addToGallery} style={{display: "none"}} />
                                {/* <Button type="button" variant="primary" size="compact" >Upload</Button> */}
                              </div>
                      </div>

                


                     

                            <div className="form-group text-center">
                            <Button type="button" onClick={(e) => this.submit(e, 0)} variant="primary-outline" size="big mr-2" >Save Draft</Button>
                                <Button variant="primary" size="big" >Post Request</Button>
                                {/* <Button type="button" variant="primary-outline" className="ml-2" size="big" onClick={(e) => this.review(e)}>Review</Button> */}
                            </div>

                  </form>
                </div>
              

      )
    }
}

export default GigRequestTab;
